import { useState } from 'react';
import axios from 'axios';
import QRCodeDisplay from './QRCodeDisplay';

export default function URLForm() {
  const [longUrl, setLongUrl] = useState('');
  const [shortUrl, setShortUrl] = useState('');
  const [shortCode, setShortCode] = useState('');
  const [showQR, setShowQR] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const res = await axios.post('http://localhost:8000/api/v1', {
        url: longUrl,
        expiry: 24,
      });
      console.log("Shortened URL response:", res);
      console.log(res.data);
      const { short } = res.data;
      setShortUrl(short);
      const parts = short.split('/');
      setShortCode(parts[parts.length - 1]);
      setShowQR(false); // reset
    } catch (error) {
      console.error(error);
      alert("Error shortening URL");
    }
  };

  return (
    <div className="max-w-xl mx-auto p-4 bg-white rounded-xl shadow-md text-center">
      <h1 className="text-xl font-semibold mb-4">URL Shortener</h1>
      <form onSubmit={handleSubmit} className="flex flex-col gap-4">
        <input
          type="text"
          placeholder="Enter long URL"
          className="border p-2 rounded"
          value={longUrl}
          onChange={(e) => setLongUrl(e.target.value)}
          required
        />
        <button type="submit" className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700">
          Shorten URL
        </button>
      </form>

      {shortUrl && (
        <div className="mt-6">
          <p className="text-green-700 font-medium">
            Short URL: <a href={shortUrl} target="_blank" rel="noreferrer" className="underline">{shortUrl}</a>
          </p>
          <button
            onClick={() => setShowQR(true)}
            className="mt-2 bg-gray-700 text-white px-3 py-1 rounded"
          >
            Generate QR Code
          </button>
          {showQR && <QRCodeDisplay code={shortCode} />}
        </div>
      )}
    </div>
  );
}
